import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

const StyledNav = styled.nav`
  background: white;
  border-bottom: 4px solid black;
  position: fixed;
  text-align: center;
  top: 0;
  width: 100%;
`;

const StyledUL = styled.ul`
  list-style-type: none;
  margin: 0;
  padding: 0;
`;

const StyledLI = styled.li`
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
`;

const StyledLink = styled(Link)`
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 16px 32px;

  :hover {
    background: black;
    color: white;
  }
`;

const data = [
  {
    id: 0,
    url: '/',
    label: 'Home',
  },
  {
    id: 1,
    url: '/about',
    label: 'About',
  },
  {
    id: 2,
    url: '/articles-list',
    label: 'Articles',
  },
];

const NavBar = () => (
  <StyledNav>
    <StyledUL>
      {data.map(({ id, url, label }) => (
        <StyledLI key={id}>
          <StyledLink to={url}>{label}</StyledLink>
        </StyledLI>
      ))}
    </StyledUL>
  </StyledNav>
);

export default NavBar;
