import React from 'react';
import styled from 'styled-components';
import { func, number, string } from 'prop-types';

const StyledButton = styled.button`
  display: inline-block;
  margin-right: 16px;
  padding: 16px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background-color: black;
  color: white;
  border-radius: 8px;
  cursor: pointer;
`;

const StyledP = styled.p`
  display: inline-block;
  margin-right: 16px;
`;

const UpvotesSection = ({ articleName, upvotes, setArticleInfo }) => {
  const upvoteArticle = async () => {
    const result = await fetch(`/api/articles/${articleName}/upvotes`, { method: 'post' });
    const body = await result.json();
    setArticleInfo(body);
  };
  return (
    <>
      <StyledButton onClick={upvoteArticle}>Add Upvote</StyledButton>
      <StyledP>{`This article has been upvoted ${upvotes} times`}</StyledP>
    </>
  );
};

UpvotesSection.propTypes = {
  articleName: string.isRequired,
  upvotes: number.isRequired,
  setArticleInfo: func.isRequired,
};

export default UpvotesSection;
