import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { shape, object } from 'prop-types';
import UpvotesSection from '../components/UpvotesSection';
import CommentsList from '../components/CommentsList';
import AddCommentForm from '../components/AddCommentForm';
import ArticlesList from '../components/ArticlesList';
import NotFoundPage from './NotFoundPage';
import articleContent from './articles-content.json';

const StyledH3 = styled.h3`
  margin-bottom: 0;
`;

const ArticlePage = ({ match: { params: { name } } }) => {
  const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });
  useEffect(
    () => {
      const fetchData = async () => {
        const result = await fetch(`/api/articles/${name}`);
        const body = await result.json();
        setArticleInfo(body);
      };
      fetchData();
    },
    [name],
  );
  const article = articleContent.find((item) => item.name === name);
  const otherArticles = articleContent.filter((item) => item.name !== name);
  return article
    ? (
      <>
        <h1>{article.title}</h1>
        <UpvotesSection
          articleName={name}
          upvotes={articleInfo.upvotes}
          setArticleInfo={setArticleInfo}
        />
        {article.content.map(({ id, text }) => (
          <p key={id}>{text}</p>
        ))}
        <CommentsList comments={articleInfo.comments} />
        <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />
        <StyledH3>Other Articles:</StyledH3>
        <ArticlesList articles={otherArticles} />
      </>
    )
    : <NotFoundPage />;
};

ArticlePage.propTypes = {
  match: shape({ params: object }).isRequired,
};

export default ArticlePage;
