import React, { useState } from 'react';
import styled from 'styled-components';
import { string, func } from 'prop-types';

const StyledDiv = styled.div`
  border-bottom: 2px solid black;
  padding: 16px;
`;

const StyledH3 = styled.h3`
  margin-bottom: 16px;
`;

const StyledInput = styled.input`
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
`;

const StyledTextarea = styled.textarea`
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
`;

const StyledButton = styled.button`
  padding: 16px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background-color: black;
  color: white;
  border-radius: 8px;
  cursor: pointer;
  width: 320px;
`;

const AddCommentForm = ({ articleName, setArticleInfo }) => {
  const [username, setUsername] = useState('');
  const [commentText, setCommentText] = useState('');

  const addComment = async () => {
    const result = await fetch(`/api/articles/${articleName}/add-comment`, {
      method: 'post',
      body: JSON.stringify({ username, text: commentText }),
      headers: {
        'Content-Type': 'application/json',
      },
    });
    const body = await result.json();
    setArticleInfo(body);
    setUsername('');
    setCommentText('');
  };

  return (
    <StyledDiv>
      <StyledH3>Add a Comment</StyledH3>
      <label>
        Name:
        <StyledInput type="text" value={username} onChange={(event) => setUsername(event.target.value)} />
      </label>
      <label>
        Comment:
        <StyledTextarea rows="4" cols="50" value={commentText} onChange={(event) => setCommentText(event.target.value)} />
      </label>
      <StyledButton onClick={addComment}>Add Comment</StyledButton>
    </StyledDiv>
  );
};

AddCommentForm.propTypes = {
  articleName: string.isRequired,
  setArticleInfo: func.isRequired,
};

export default AddCommentForm;
